<template>
  <div class="wallet">
    <s-header :name="'Wallet'"></s-header>
    <div class="menoy">
      <div class="item-img pic">
        <img src="@/assets/money.png" alt="">
      </div>
      <div  class="item-img my-wallet">
        my wallet
      </div>
      <div  class="item-img my-money">
        ₹{{rest}} 
      </div>
    </div>
  <div class="rechange">
        <el-button  type="primary" @click="btnPay">Recharge</el-button>
        <el-button class="green" @click="btnDraw">Withdraw</el-button>
        <!-- <el-button type="primary" @click="btnQuery">查询余额</el-button> -->
      </div>
  </div>
</template>

<script>
import sHeader from "@/components/SimpleHeader";
import {getRest} from '../service/golddetail.js'
import { insertPay, wePay, queryBalance, payment } from "../service/pay.js";
import { Toast } from 'vant';
export default {
  components: {
    sHeader
  },
  data() {
    return {
      rest:""
    };
  },
  created() {
    Toast.loading({
      message: 'Loading...',
      forbidClick: true
    });
      getRest().then(res=>{
     this.rest = res.data.rest;
  //  this.levels[0].content = this.rest
    // console.log(res);
    Toast.clear()
    }).catch(err=>{
      // console.log("err",err);
    });
  },
  methods: {
    btnPay(){
       this.$router.push({ path: "bank" })
    },
    btnDraw(){
       this.$router.push({ path: "draw" })
      Toast.fail(
        'Not yet open'
      )

}
  },

};
</script>

<style lang="less"  scoped>
.wallet {
  box-sizing: border-box;
  padding: 20px;
   .menoy{
     margin-top: 50px;
     display: flex;
     flex-direction: column;
     justify-content: center;
     .pic{
       margin-bottom: 20px;
     }
     .item-img{
       display: flex;
       justify-content: center;
       margin: 15px 0;
      img{
        width: 60px;
        height: 60px;
      }
     }
     .my-wallet{
        margin-top:10px;
        margin-bottom: 0;
       font-size: 18px;
     }
     .my-money{
       font-size: 30px;
       font-weight: 600;
     }
   }
      .rechange {
        margin-top: 200px;
      text-align: center;
      .green{
        color: #1ba160;
        font-weight: 500;
      }
      .el-button {
        // margin-top: 10px;
        margin: 10px auto;
        width: 55%;
      }
    }
}
</style>
