<template>
  <div class="bank">
    <s-header :name="'Wallet'"></s-header>
    <div class="bank-body">
      <div class="rechange">
        <el-input v-model.number="pay" type="number" placeholder="Please enter the recharge amount"></el-input>
        <el-button type="primary" @click="btnPay">Determine the top-up</el-button>
        <!-- <el-button type="primary" @click="btnQuery">查询余额</el-button> -->
      </div>
    </div>
  </div>
</template>

<script>
import sHeader from "@/components/SimpleHeader";
import { insertPay, wePay, queryBalance, payment } from "../service/pay.js";
import { Toast } from 'vant';
export default {
  components: {
    sHeader
  },
  data() {
    return {
      pay: "",
      gettime: "",
      time: ""
    };
  },
  methods: {
    getCurrentTime() {
      //获取当前时间并打印
      var _this = this;
      _this.time = new Date().getTime();
      // console.log(_this.time);
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? "0" + new Date().getSeconds()
          : new Date().getSeconds();
      _this.gettime = yy + "-" + mm + "-" + dd + " " + hh + ":" + mf + ":" + ss;
      _this.merordercode = `${yy}${mm}${dd}${hh}${mf}${ss}`;
      // console.log(_this.gettime);
    },
    // btnQuery() {
    //   queryBalance({
    //     sign_type: "MD5",
    //     sign: "imzdixz6zub3jc2whtcw47wxhsluuy5y",
    //     mch_id: "100999001"
    //   }).then(res => {
    //     console.log(res);
    //   });
    // },
    btnPay() {
      //  console.log(this.pay);
      this.getCurrentTime();
      if (this.pay != "") {
        // console.log(this.pay);
        // let hmacstr = `code=11172&merordercode=${this.merordercode}&notifyurl=www.baidu.com&callbackurl=www.baidu.com&amount=${this.pay}&key=i6Dp3BvXRQHSFf8C9hQPalD01waZUgTM`;
        // console.log(hmacstr);
        // let sign = this.$md5(hmacstr);
        // sign = sign.toUpperCase();
        //  let time = new Date().getTime();
        //  console.log(time);
        // console.log(sign);
        if(this.pay>=10000 &&this.pay<=100000){
            payment({
          mchId: "11172",
          merchantKey: "i6Dp3BvXRQHSFf8C9hQPalD01waZUgTM",
          payType: "909",
          tradeAmount: this.pay
        }).then(res => {
          // console.log("res", res);
           let url = res.data.data.checkstand;
          //  console.log(url);
           var u = navigator.userAgent;
var isAndroid = u.indexOf("Android") > -1 || u.indexOf("Adr") > -1; //android终端
var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
           if(url){
             if(isAndroid) {  //android终端
			  	// let url = res.data.url;
			  	window.open(url);
		 	}else if(isiOS) {   //ios终端
				window.location.href = url;
	 		}
            //  window.open(url, '_blank') //_self 在当前窗口显示目标网页   _top：框架网页中在上部窗口中显示目标网页
            // window.location.href = url
           }
             
        }).catch(err=>{
          //  console.log("错误", err);
        });
        }else if(this.pay<10000){
          Toast.fail("The minimum rechange is 10000")

        }else{
           Toast.fail("The maximum rechange is 100000")
        }
      
        // wePay({
        //   code: "11172",
        //   paycode: "909",
        //   notifyurl: "www.baidu.com",
        //   amount: this.pay,
        //   callbackurl: "www.baidu.com",
        //   merordercode: this.merordercode,
        //   sign: sign,
        //   starttime: this.time
        // })
        //   .then(res => {})
        //   .catch(err => {
        //     console.log("err", err);
        //   });
      }
    }
  }
};
</script>

<style lang="less" scoped>
.bank {
  box-sizing: border-box;
  padding: 20px;
  .bank-body {
    margin-top: 44px;
    font-size: 16px;
    .rechange {
      text-align: center;
      .el-button {
        // margin-top: 10px;
        margin: 10px auto;
        width: 80%;
      }
    }
  }
}
</style>
