<template>
  <div class="bank">
    <s-header :name="'Withdrawal'"></s-header>
    <div class="bank-body">
      <div class="rechange">
        <el-form
          :model="dynamicValidateForm"
          ref="dynamicValidateForm"
          label-width="100px"
          class="demo-dynamic"
        >
          <el-form-item
            prop="accountName"
            label="AccountName:"
           ><el-input v-model="dynamicValidateForm.accountname"></el-input>
          </el-form-item>
           <el-form-item
            prop="amount"
            label="Amount:"
           ><el-input v-model="dynamicValidateForm.amount"></el-input>
          </el-form-item>
           <el-form-item
            prop="bankname"
            label="BankName:"
           ><el-input v-model="dynamicValidateForm.bankname"></el-input>
          </el-form-item>
           <el-form-item
            prop="cardnumber"
            label="Cardnumber:"
           ><el-input v-model="dynamicValidateForm.cardnumber"></el-input>
          </el-form-item>
           <el-form-item
            prop="code"
            label="Code:"
           ><el-input v-model="dynamicValidateForm.code"></el-input>
          </el-form-item> 
          <el-form-item
          class="el-item-email"
            prop="email"
            label="email:"
            :rules="[
      { required: true, message: '请输入邮箱地址', trigger: 'blur' },
      { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }
    ]"><el-input v-model="dynamicValidateForm.email"></el-input>
          </el-form-item>
            <el-form-item
            prop="ifsc"
            label="Ifsc:"
           ><el-input v-model="dynamicValidateForm.ifsc"></el-input>
          </el-form-item>
            <el-form-item
            prop="key"
            label="Key:"
           ><el-input v-model="dynamicValidateForm.ifsc"></el-input>
          </el-form-item>
            <el-form-item
            prop="mobile"
            label="Mobile:"
           ><el-input type="number" v-model="dynamicValidateForm.mobile"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('dynamicValidateForm')">Submit</el-button>
          </el-form-item>
        </el-form>
        <!-- <div class="row"></div>
           <el-input v-model="accountname"  placeholder="Please enter the accountName"></el-input>
           <el-input v-model.number="amount" type="number" placeholder="Please enter the amount"></el-input>
          <el-input v-model="cardnumber"  placeholder="Please enter the cardnumber"></el-input>
         <el-input v-model="code"  placeholder="Please enter the code"></el-input>
          <el-input v-model="email" type="email"  placeholder="Please enter the email"></el-input>
          <el-input v-model="ifsc"  placeholder="Please enter the ifsc"></el-input>
            <el-input v-model="key"  placeholder="Please enter the key"></el-input>
        <el-input type="text" placeholder="Enter your phone number" v-model="phone" maxlength="10" show-word-limit></el-input>
        <el-button type="primary" @click="btnDraw">Withdraw</el-button>-->
        <!-- <el-button type="primary" @click="btnQuery">查询余额</el-button> -->
      </div>
    </div>
  </div>
</template>

<script>
import sHeader from "@/components/SimpleHeader";
import { insertPay, wePay, queryBalance, payment,Deposit } from "../service/pay.js";
import { Toast } from "vant";
export default {
  components: {
    sHeader
  },
  data() {
    return {
    
      gettime: "",
      time: "",
      dynamicValidateForm: {
        accountname:"",
        amount:"",
        bankname:"",
        cardnumber:"",
        code:"",
        email: "",
        ifsc:"",
        key:"",
        mobile:""

      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          Toast.fail("please enter your vaild email")
        } else {
          if(accountname!=""&&amount!=""&&bankname!=""&&cardnumber!=""&&code!=""&&ifsc!=""&&key!=""&&mobile!=""){
            let params = {
              accountname:this.dynamicValidateForm.accountname,
              amount:this.dynamicValidateForm.amount,
              bankname:this.dynamicValidateForm.bankname,
              cardnumber:this.dynamicValidateForm.cardnumber,
              code:this.dynamicValidateForm.code,
              email:this.dynamicValidateForm.email,
              ifsc:this.dynamicValidateForm.ifsc,
              key:this.dynamicValidateForm.key,
              mobile:this.dynamicValidateForm.mobile
            }
            Deposit(params).then(res=>{
              Toast.success("success")
            }).catch(err=>{
                Toast.fail("Withdrawal failed")
            })
          }else{
            Toast.fail("Please enter the complete value!")
          }
        }
      });
    },
  
    // removeDomain(item) {
    //   var index = this.dynamicValidateForm.domains.indexOf(item);
    //   if (index !== -1) {
    //     this.dynamicValidateForm.domains.splice(index, 1);
    //   }
    // },
 
    // btnQuery() {
    //   queryBalance({
    //     sign_type: "MD5",
    //     sign: "imzdixz6zub3jc2whtcw47wxhsluuy5y",
    //     mch_id: "100999001"
    //   }).then(res => {
    //     console.log(res);
    //   });
    // },
    btnDraw() {
      //  console.log(this.pay);
      this.getCurrentTime();
      if (this.pay != "") {
        // console.log(this.pay);
        // let hmacstr = `code=11172&merordercode=${this.merordercode}&notifyurl=www.baidu.com&callbackurl=www.baidu.com&amount=${this.pay}&key=i6Dp3BvXRQHSFf8C9hQPalD01waZUgTM`;
        // console.log(hmacstr);
        // let sign = this.$md5(hmacstr);
        // sign = sign.toUpperCase();
        //  let time = new Date().getTime();
        //  console.log(time);
        // console.log(sign);
        if (this.pay >= 10000 && this.pay <= 100000) {
          payment({
            mchId: "11172",
            merchantKey: "i6Dp3BvXRQHSFf8C9hQPalD01waZUgTM",
            payType: "909",
            tradeAmount: this.pay
          })
            .then(res => {
              // console.log("res", res);
              let url = res.data.data.checkstand;
              //  console.log(url);
              var u = navigator.userAgent;
              var isAndroid =
                u.indexOf("Android") > -1 || u.indexOf("Adr") > -1; //android终端
              var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
              if (url) {
                if (isAndroid) {
                  //android终端
                  // let url = res.data.url;
                  window.open(url);
                } else if (isiOS) {
                  //ios终端
                  window.location.href = url;
                }
                //  window.open(url, '_blank') //_self 在当前窗口显示目标网页   _top：框架网页中在上部窗口中显示目标网页
                // window.location.href = url
              }
            })
            .catch(err => {
              // console.log("错误", err);
            });
        } else if (this.pay < 10000) {
          Toast.fail("The minimum rechange is 10000");
        } else {
          Toast.fail("The maximum rechange is 100000");
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
// .el-form-item__label{
//   padding-left: 0px;
// }
.bank {
  box-sizing: border-box;
  padding: 20px;
  .bank-body {
    margin-top: 44px;
    font-size: 16px;
    .rechange {
      text-align: center;
      .el-form{
        .el-form-item{
          margin-bottom: 10px;
        }
        .el-item-email{
          margin-bottom: 18px;
        }
      }
      .el-button {
        // margin-top: 10px;
        margin: 10px auto;
        width: 80%;
      }
    }
  }
}
</style>
